var superagent = require("superagent");
var isBrowser = typeof window !== "undefined";
var initToken = require("./tokenRetry/initToken");
var getLocationContext = require('./locationContext/getLocationContext');
var serviceConfig = require("./config");

var HOST = "unix:///run/spire/sockets/agent.sock";
var authNZService, authHelper;

if (!isBrowser) {
  authHelper = __non_webpack_require__("auth-node-helper");
}

var httpAgent, httpsAgent;

// importing the http agent only for sever side
if (!isBrowser) {
  // node http agent
  var http = __non_webpack_require__("http");
  httpAgent = new http.Agent({ keepAlive: true, maxSockets: 24 });
// node https agent
  var https = __non_webpack_require__("https");
  httpsAgent = new https.Agent({ keepAlive: true, maxSockets: 24 });
}

// 5 minutes superagent-cache
var defaults = {expiration: 300};

if (isBrowser) {
  // arguments: (superagent instance, pre-instantiated cache module, defaults)
  require("superagent-cache")(superagent, null, defaults);
}

require('superagent-promise')(
    superagent,
    Promise
);

require("./tokenRetry")(superagent);

var defaultConfig = Object.assign({
  root: "",
  path: "",
  timeout: isBrowser ? 15000 : 5000,
  maxRetry: 1,
  statusCodeToRetry: [401, 408, 503, 522, 524]
}, serviceConfig);

var methods = ["get", "post", "put", "head", "del", "options"];

function updateConfig(config) {
  if(config && typeof config === 'object') {
    defaultConfig = Object.assign({}, defaultConfig, config);
  }
}

function initializeAuthService(config = defaultConfig) {
  if (!authNZService && authHelper) {
    authNZService = authHelper({
        url: HOST,
        spiffeId: config.spiffeID,
        audience: config.audience,
        timeToRefreshToken: 30,
        grpcTimeoutInMs: 500
    });
  }
}
function service(config, origReq) {
  if (!(this instanceof service)) {
    return new service(config, origReq);
  }
  // applicable only for the node.js. express request object.
  this.origReq = origReq;
  if (typeof config == "undefined") {
    this.config = Object.assign({}, defaultConfig);
  } else if (typeof config == "string") {
    this.config = Object.assign({}, defaultConfig, {
      path: config
    });
  } else {
    this.config = Object.assign({}, defaultConfig, config);
  }

  this.url = this.config.url || this.config.root + this.config.path;
  this.handleErrors = handleErrors;
  this.initToken = initToken;
}

function methodize(proto, method) {
  proto[method] = function(uri) {
    var url = this.url + (uri || "");
    this.agent = superagent[method](url);

    // using the node agent for server side calls
    if (!isBrowser && typeof url === "string") {
      if (url.indexOf("https") === 0 && httpsAgent) {
        this.agent.agent(httpsAgent);
      } else if (url.indexOf("http") === 0 && httpAgent) {
        this.agent.agent(httpAgent);
      }
    }

    this.agent.on("error", function(err) {
      var reqObj = this || {};
      var reqMessage = "REQUEST=" + reqObj.url + " METHOD=" + reqObj.method;
      console.error("[M-AGENT DefaultErrorCatch]" + reqMessage + getErrorLog(err));
    });
    this.agent.retry(this.config.maxRetry, this.config.statusCodeToRetry);
    this.agent.timeout(this.config.timeout);
    !isBrowser && this.agent.set("Connection", "Keep-Alive");
    this.agent.set("X-myntraweb", "Yes");
    if (url.includes("dockins.myntra.com")){
      this.agent.set("x-myntra-network", "yes");
    }
    isBrowser && this.agent.set("X-Requested-With", "browser");
    this.config.auth && this.agent.set("authorization", this.config.auth);
    //if config.headers has present it will take the ultimate priority.
    //user auth added before this on purpose so there is still a chance for overriding by a dev.
    this.config.headers && this.agent.set(this.config.headers);

    var locationContext = getLocationContext();
    locationContext && this.agent.set(locationContext);

    if (isBrowser) {
      this.agent.withCredentials();
      this.agent.set('x-meta-app', 'channel=web');
    }

    //add agent.specialEnd()  to handle default error case
    return agentOverrider(this.agent, this.origReq, this.config);
  };
  return proto;
}

var agentOverrider = function(agent, origRq, config) {
  agent.setTokenServiceConfig(config);

  var _end = agent.end;
  agent.end = function(cb) {
    var startTime = Date.now();
    _end.call(agent, function(err, res, key) {
      //log(agent, res, cfg, err);
      if (res) {
        res.timeTaken = Date.now() - startTime;
        var updatedTokens = res.request && res.request.updatedTokens;
        var authNZFail = res.request && res.request.authNZFail;
        if (updatedTokens) {
          res.updatedTokens = updatedTokens;
        }
        if (authNZFail) {
          res.authNZFail = authNZFail;
        }
      }
      cb && cb(err, res, key);
    });
    return agent;
  };

  agent.withAuthNZ = function(flag) {
    if (typeof flag === 'boolean' && flag === false) {
      return Promise.resolve();
    }

    return new Promise(function(resolve) {
      agent.setAuthNZHeader(authNZService, config.spiffeID).then(function() {
        resolve();
      }).catch(function(e) {
        console.error('[Error setting authN header]', e);
        resolve();
      });
    });
  };

  return agent;
};

for (var i = 0; i < methods.length; i++) {
  methodize(service.prototype, methods[i]);
}

function handleRedirection(path) {
  if (typeof window !== "undefined") {
    window.rrhistory ? window.rrhistory.push(path) : (window.location.href = path);
  } else {
    nodeRes.redirect(path);
  }
}

function getURL(err) {
  if (!err) return " REQUEST=[NO SERVICE URL]";
  var r = err.response;
  return " REQUEST=" + (r && r.request && r.request.url) || "[NONE]";
}
function getRouteURL(nodeRes) {
  if (!nodeRes) return " EXPRESS.ROUTE=[NO EXPRESS URL]";
  return " EXPRESS.ROUTE=" + nodeRes.req.get("host") + nodeRes.req.originalUrl;
}
function getErrorLog(err, res, nodeRes) {
  var errorStatus =
      " ERROR.STATUS=" + (err && err.status ? err.status : "[NO STATUS CODE]");
  var errorMessage =
      " ERROR.MESSAGE=" + (err && err.message ? err.message : "[NO MESSAGE]");
  var errorCodeMessage =
      " ERROR.CODE=" + (err && err.code ? err.code : "[NO CODE]");
  return errorStatus + errorMessage + errorCodeMessage ;
}

function handleErrors(err, res, nodeRes) {
  //returns true if it finds any error in either err or res object.
  var message = "[M-AGENT SmartErrorHandler]" + getRouteURL(nodeRes);
  if (err) {
    if (err.timeout) {
      console.error(message, "REASON=[TIMED OUT]");
    } else if (err.status === 404) {
      console.error(message, "REASON=[NOT FOUND]");
      //handleRedirection('/notFound');
    } else if (err.status === 403) {
      //unauthorized
      console.error(message, "REASON=[UNAUTHORIZED REQUEST REDIRECTED]");
      var path = "/login?referer=" + window.location.href;
      handleRedirection(path);
    } else {
      console.error(message, "\n[STACK]=>\n", err.stack);
    }
    return true;
  } else if (res) {
    var body = res.body;
    if (!body) {
      console.error(message, "REASON=[EMPTY BODY RESPONSE]");
      // Response body will be empty in some cases for ex: when response type is text/html
      return true;
    }
    if (body === "Not Found") {
      //biro errors throw 200 and "Not Found".
      console.error(message, 'REASON=[BODY CONTAINS "NOT FOUND"]');
      return true;
    }
    if (body.error) {
      console.error(
          message,
          "[BODY.ERROR]",
          body.error,
          "[BODY.ERROR.MESSAGE]",
          body.error.message
      );
      return true;
    } else if (body.status && body.status.statusType && body.status.statusType !== "SUCCESS") {
      console.error(
          message,
          "[BODY.STATUS.MESSAGE]",
          body.status.statusMessage
      );
      return true;
    } else if (body.meta && body.meta.errorType) {
      console.error(
          message,
          "[BODY.META.ERRORTYPE]",
          body.meta.errorType,
          "[BODY.META.code]",
          body.meta.code,
          "[BODY.META.errorDetail]",
          body.meta.errorDetail
      );
      return true;
    }
  }
  return false;
}

service.smartError = handleErrors;
service.updateConfig = updateConfig;
service.initializeAuthService = initializeAuthService;
module.exports = service;
