/**
 * Add to the request prototype.
 */
var shouldRetry = require('./should-retry');
var initToken = require("./initToken");

module.exports = function (superagent) {
    var Request = superagent.Request;
    Request.prototype.oldRetry = Request.prototype.retry;
    Request.prototype.retry = retry;
    Request.prototype.callback = callback;
    Request.prototype.setAuthNZHeader = setAuthNZHeader;
    Request.prototype.setTokenServiceConfig = setTokenServiceConfig;
    return superagent;
};

/**
 * Override Request callback to set a retry a token
 *
 * This overrides crucial behaviour: it will retry on ANY error (eg 401...) due to shouldRetry having
 * different behaviour.
 *
 * @param err
 * @param res
 * @return {Object}
 */
function callback(err, res) {
    var authenticateHeader;

    if (res && res.headers) {
        authenticateHeader = res.headers['www-authenticate'] || res.headers['WWW-Authenticate'];
    }

    if (this._maxRetries && this._retries++ < this._maxRetries && shouldRetry(err, res, this._statusCodesToRetry)) {
        var req = this;
        
        // make a call for token and than retry the original request
        if (res && res.status === 401) {
            if (authenticateHeader) {
                console.error('[www-authenticate]', authenticateHeader);
                req.authNZFail = true;
            } else {
                return initToken(req);
            }
        } else {
            return req._retry();
        }
    }

    if (res && res.status === 403 && authenticateHeader) {
        console.error('[www-authenticate]', authenticateHeader);
        this.authNZFail = true;
    }

    var fn = this._callback;
    this.clearTimeout();

    if (err) {
        if (this._maxRetries) err.retries = this._retries - 1;
        this.emit('error', err);
    }

    fn(err, res);
}

/**
 * Override Request retry to also set a token retry
 *
 * @param {Number} retries
 * @return {retry}
 */

function retry(maxRetries, statusCodes) {
    if (maxRetries <= 0) {
        maxRetries = 0;
    }

    this._maxRetries = maxRetries;
    this._statusCodesToRetry = statusCodes;
    this._retries = 0;
    return this
}

function setAuthNZHeader (authNZService, spiffeID) {
    var self = this;

    return new Promise(function(resolve, reject) {
        if (authNZService && typeof authNZService.fetchJWTSVID === 'function') {
            authNZService.fetchJWTSVID().then(function(r) {
                if (r && r.data && r.data.svids) {
                    var svid = (
                        r.data.svids.find(function(entry) {
                            return entry.spiffe_id === spiffeID;
                        }) || {}
                    ).svid;
                    if (svid) {
                        self.auth(svid, { type: 'bearer' });
                        self.set("Authorization-Meta", "Version MYN_1.0");
                        resolve();
                    } else {
                        reject(`SVID for ${spiffeID} not available`);
                    }
                } else {
                    reject('No SVIDs available');
                }
            }).catch(function(err) {
                const errorMessage = (err && err.message) ? err.message : err;
                reject(errorMessage);
            });
        } else {
            reject('AuthNZ service not available');
        }
    });
}

function setTokenServiceConfig(config) {
  this.tokenServiceConfig = config;
}
