var superagent = require("superagent");

var isBrowser = typeof window !== "undefined";
var cookies = require("../cookies");

function retryRequestAfterTokenFetch(tokenPath, req, isLoggedin, requestTokens, config) {
    if (isBrowser) {
        return superagent
            .get(tokenPath)
            .forceUpdate(true)
            .set("clientid", config.clientId)
            .set("x-meta-app", "channel=web")
            .withCredentials()
            .retry(0)
            .timeout(15000)
            .end(function (err, res) {
                if (isLoggedin === "true" && res && (res.status === 401)) {
                    var _getTokenEndpoint = config.services.getToken;

                    cookies.del('ilgim');

                    return superagent
                        .get(_getTokenEndpoint)
                        .forceUpdate(true)
                        .set("clientid", config.clientId)
                        .withCredentials()
                        .retry(0)
                        .timeout(15000)
                        .end(function (err, res) {
                            req && req._retry();
                        });
                } else {
                    req && req._retry();
                }
            });
    } else {
        if (isLoggedin === "true") {
            var headers = {
                clientid: config.clientId,
                at: requestTokens.at,
                rt: requestTokens.rt,
                "x-myntra-app": "appFamily=MyntraRetailiOS; appVersion=5.2101.1;"
            };
            if (requestTokens.xid) {
                headers.xid = requestTokens.xid;
            }
            return superagent
                .get(tokenPath)
                .set(headers)
                .retry(0)
                .timeout(5000)
                .end(function (err, res) {
                    if (res && res.status === 401) {
                        var _getTokenEndpoint = config.services.getToken;

                        return superagent
                            .get(_getTokenEndpoint)
                            .set("clientid", config.clientId)
                            .set("x-myntra-app", "appFamily=MyntraRetailiOS; appVersion=5.2101.1;")
                            .retry(0)
                            .timeout(5000)
                            .end(function (err, res) {
                                var at = (res && res.headers && res.headers.at) || "";
                                var xid = (res && res.headers && res.headers.xid) || "";

                                if (at && req) {
                                    req.updatedTokens = { 'at': at, rtExpired: true };
                                    if (xid) {
                                        req.updatedTokens.xid = xid;
                                    }
                                    req.set("at", at);
                                }

                                return req && req._retry();
                            });
                    } else {
                        var at = (res && res.headers && res.headers.at) || "";
                        var rt = (res && res.headers && res.headers.rt) || "";
                        var sxid = (res && res.headers && res.headers.sxid) || "";
                        var xid = (res && res.headers && res.headers.xid) || "";

                        if (at && rt && req) {
                            req.updatedTokens = {'at': at, 'rt': rt};
                            if (xid) {
                                req.updatedTokens.xid = xid;
                            }
                            if (sxid) {
                                req.updatedTokens.sxid = sxid;
                            }
                            // setting at & rt in the original request
                            req.set("at", at);
                            req.set("rt", rt);
                        }
                        return req && req._retry();
                    }
                });
        } else {
            var headers = {
                clientid: config.clientId,
                "x-myntra-app": "appFamily=MyntraRetailiOS; appVersion=5.2101.1;"
            };
            if (requestTokens.xid) {
                headers.xid = requestTokens.xid;
            }
            return superagent
                .get(tokenPath)
                .set(headers)
                .retry(0)
                .timeout(5000)
                .end(function (err, res) {
                    var at = (res && res.headers && res.headers.at) || "";
                    var xid = (res && res.headers && res.headers.xid) || "";

                    if (at && req) {
                        req.updatedTokens = { 'at': at };
                        if (xid) {
                            req.updatedTokens.xid = xid;
                        }
                        req.set("at", at);
                    }

                    return req && req._retry();
                });
        }
    }
}

module.exports = function initToken(req) {
    var config = req.tokenServiceConfig || require("../config");
    // check for is logged in cookie
    // on the basis of logged in state the token path is chosen.
    var isLoggedin = "false";
    var requestTokens = {};

    if (isBrowser) {
        isLoggedin = cookies.get('ilgim') || "false";
    } else {
        var at = req && req.header && req.header.at;
        var rt = req && req.header && req.header.rt;
        var xid = (req && req.header && req.header.xid) || ''

        if (xid) {
          requestTokens.xid = xid;
        }

        if (at) {
            requestTokens.at = at;
        }

        if (rt) {
            requestTokens.rt = rt;
            isLoggedin = "true"
        }
    }

    var tokenPath = config.services.getToken;

    if (isLoggedin === "true") {
        tokenPath = config.services.refreshToken;
    }

    // if req is present it means token 401 error came
    // in this case retry the original request after the token fetch.
    if (req) {
        retryRequestAfterTokenFetch(tokenPath, req, isLoggedin, requestTokens, config);
    } else if (isBrowser) {
        superagent
            .get(tokenPath)
            .set("clientid", config.clientId)
            .set("x-meta-app", "channel=web")
            .retry(0)
            .timeout(15000)
            .withCredentials()
            .end(function (err, res) {
                if (err) {
                    console.log('error');
                }
                console.log('token fetched and updated in the browser');
            });
    }
};
