//targetFile = targetFile === "development" ? "default" : targetFile; //if node_env is development then also use default.
//can do deep merge of multiple files. if need. not doing right now as configs are simple and have very few urls.
var config = {};

if (typeof window === 'undefined') {
    var targetFile = (process && process.env.NODE_ENV) || 'default';
    config = require('./' + targetFile);
} else {
    var prefix = window.location.hostname === "www.myntra.com" ? "" : "fox-";

    if (prefix === 'fox-') {
        var targetFile = 'release';

        if (window.location.host && (window.location.host.indexOf('dev.myntra.com') !== -1)) {
            targetFile = 'development';
        }

        config = require('./' + targetFile);
    } else {
        var targetFile = 'production';
        config = require('./' + targetFile);
    }
}

module.exports = config;
