var isBrowser = typeof window !== "undefined";

var prefix = '';

if (isBrowser) {
    prefix = window.location.hostname === "www.myntra.com" ? "" : "fox-";
}

var domain = ".myntra.com";

var cookies = {
    get: function(key) {
        key = prefix + key;
        var result = new RegExp('(?:^|; )' + encodeURIComponent(key) + '=([^;]*)').exec(document.cookie);
        return result ? result[1] : null;
    },

    set: function(key, value, days, path) {
        key = prefix + key;
        var cookie = encodeURIComponent(key) + '=' + encodeURIComponent(value);
        if (days) {
            var expDate = new Date(Date.now() + (days * 24 * 60 * 60 * 1000));
            cookie += '; expires=' + expDate.toGMTString();
        }
        if (!path) { path = '/'; }
        cookie += '; path=' + path;
        cookie += '; domain=' +  domain;
        document.cookie = cookie;
    },

    del: function(key) {
        this.set(key, '', -1);
    }
};

module.exports = cookies;
