var ERROR_CODES = [
    'ECONNRESET',
    'ETIMEDOUT',
    'EADDRINFO',
    'ESOCKETTIMEDOUT'
];

/**
 * Determine if a request should be retried.
 * (Borrowed from segmentio/superagent-retry)
 *
 * @param {Error} err
 * @param {Response} [res]
 * @returns {Boolean}
 */
module.exports = function shouldRetry(err, res, statusCodesToRetry) {
    if (!statusCodesToRetry) {
        statusCodesToRetry = [401, 408, 503, 522, 524]
    }
    if (res && res.status && statusCodesToRetry.indexOf(res.status) > -1) return true;
    if (res && res.status && res.status === 401) return true;
    if (err && err.code && ERROR_CODES.indexOf(err.code) > -1) return true;
    // Superagent timeout
    if (err && 'timeout' in err && err.code == 'ECONNABORTED') return false;
    if (err && 'crossDomain' in err) return true;
    return false;
};
