const cookies = require("../cookies");

function getCookie(name) {
    const rawCookie = cookies.get(name);
    return rawCookie && decodeURIComponent(rawCookie);
}

function getMatch(regex, str) {
    const result = regex.exec(str);
    if (Array.isArray(result) && result.length > 1) {
        return result[1];
    }
    else {
        return null;
    }
}

function getPincodeAndSource() {
    const pincodeCookie = getCookie("mynt-ulc-api");
    const sourceCookie = getCookie("mynt-loc-src");

    if (pincodeCookie && sourceCookie) {
        const pincode = getMatch(/pincode:(\d{6})/i, pincodeCookie);
        const source = getMatch(/source:(\w{2,4})/i, sourceCookie);

        if (pincode && source) {
            return {
                pincode: pincode,
                source: source
            }
        }
    }

    return {};
}

/**
 * Cookies set from PWA/WEB.
 * [Confluence]{@link https://confluence.myntracorp.com/confluence/display/STOR/Location+Context+Feature}
 */
function getLocationContext() {
    if (typeof window === "undefined") {
        return null;
    }

    const values = getPincodeAndSource();
    const source = values.source
    const pincode = values.pincode;

    if (source && pincode) {
        return {
            "x-location-context": "pincode=" + pincode + ";source=" + source
        };
    } else {
        return null;
    }
}

module.exports = getLocationContext;
